import {
	useState,
	useEffect,
	useRef,
	useImperativeHandle,
	forwardRef,
} from "react";
import Form from "./../FormData/Form";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
	faEye,
	faTrashCan,
	faInfoCircle,
	faSearch,
	faArrowRight,
	faCheck,
} from "@fortawesome/free-solid-svg-icons";
import { FixedSizeGrid as Grid } from "react-window";
import AutoSizer from "react-virtualized-auto-sizer";
import { Db } from "../../../database/Db_new";
import styles from "./../main.module.scss";
import { toast } from "react-toastify";
import ViewDetail from "./../ViewDetail";
const GUTTER_SIZE = 16;

function splitArray(array, part) {
	var tmp = [];
	for (var i = 0; i < array.length; i += part) {
		tmp.push(array.slice(i, i + part));
	}
	return tmp;
}

// hàm lọc giá trị
async function filterData(searchKey, str) {
	return Db.transaction("rw", Db.datas, async function () {
		let data = await Db.datas
			.orderBy("_id")
			.filter((value) => {
				let valueArray = value[searchKey].split(" ");
				let strArray = str.split(" ").map((v) => v.toLowerCase());
				if (searchKey === "hanzi") {
					valueArray = [...value.hanzi];
				}

				return valueArray.some((v) => {
					return strArray.includes(v.toLowerCase());
				});
			})
			.toArray();
		return Promise.resolve({ data, count: data.length });
	});
}

async function DeleteItem(itemId) {
	await Db.datas.where("_id").equals(itemId).delete();
	return Promise.resolve;
}
async function getDataFormIndexDb(id) {
	if (id) {
		return Db.transaction("rw", Db.datas, async function () {
			let data = await Db.datas.where("tag_id").equals(id).toArray();
			return Promise.resolve(data);
		});
	}
	return Promise.resolve([]);
}

async function ModifyData(table, searchKey, modifyKey, value) {
	await Db[table]
		.where("_id")
		.equals(searchKey)
		.modify((data) => {
			data[modifyKey] = value;
		});
}

function QTest({ tag, datas }) {
	const [isReset, setIsReset] = useState(false);
	const tempQTestArrayID = useRef([]);

	const [Question, setQuestion] = useState({});
	const TestRef = useRef();
	useEffect(() => {
		(async () => {
			let tags = await Db["tags"].orderBy("id").toArray();
			return tags.filter((item) => item._id === tag._id)[0].QTestArrayID;
		})().then((value) => {
			console.log("V : ", value);
			tempQTestArrayID.current = value;
			setIsReset(value.length === datas.length);
			setQuestion(genQuestion(datas));
		});

		return async () => {
			console.log("clear up!");
			console.log(tempQTestArrayID.current);
			await ModifyData(
				"tags",
				tag._id,
				"QTestArrayID",
				tempQTestArrayID.current
			);
			tempQTestArrayID.current = [];
		};
	}, []);

	const genQuestion = (_datas) => {
		let random = Math.random();
		let testData = null;
		for (let i = 0; i < _datas.length; i++) {
			if (!tempQTestArrayID.current.includes(_datas[i]._id)) {
				testData = _datas[i];
			}
		}
		if (!testData) {
			console.log(tempQTestArrayID.current);
			//setIsReset(true);
			return;
		}
		if (random > 0.5) {
			return {
				_id: testData._id,
				q: `[ ${testData.hanzi} ] => tiếng Việt`,
				answer: `${testData.Vietnamese_meaning}`,
			};
		}
		return {
			_id: testData._id,
			q: `[ ${testData.Vietnamese_meaning} ] => tiếng Trung`,
			answer: `${testData.hanzi}`,
		};
	};

	const check = (a, b) => {
		return a.answer.trim().toLowerCase() === b.toLowerCase();
	};
	const handleSubmit = (event) => {
		event.preventDefault();
		const formData = new FormData(event.target);
		const answer = Array.from(formData.entries())[0][1];
		if (check(Question, answer.trim())) {
			tempQTestArrayID.current.push(Question._id);
			TestRef.current.style.border = "2px solid var(--LimeGreen)";
			let id = setTimeout(() => {
				TestRef.current.style.border = "2px solid var(--boder-color)";
				setQuestion(genQuestion(datas));
				clearTimeout(id);
			}, 1000);
		} else {
			TestRef.current.style.border = "2px solid var(--BrightRed)";
			let id = setTimeout(() => {
				TestRef.current.style.border = "2px solid var(--boder-color)";
				clearTimeout(id);
			}, 1000);
		}
	};
	return (
		<div className={styles["test-wrapper"]}>
			{isReset ? (
				<div className={styles["test-container"]}>
					<h3>Đã hoàn thành test , bấm reset để test lại</h3>
					<button
						type='reset'
						onClick={() => {
							ModifyData(
								"tags",
								tag._id,
								"QTestArrayID",
								[]
							).then(() => {
								setIsReset(false);
								tempQTestArrayID.current = [];
								console.log(
									"reset temp : ",
									tempQTestArrayID.current
								);
								setQuestion(genQuestion(datas));
							});
						}}
					>
						reset
					</button>
				</div>
			) : (
				<div className={styles["test-container"]} ref={TestRef}>
					<div className={styles["test-Question"]}>{Question?.q}</div>
					<div className={styles["test-Answer"]}>
						<form onSubmit={handleSubmit}>
							<input type='search' name='answer' />
							<button type='submit'>
								<FontAwesomeIcon
									color='var(--LimeGreen)'
									icon={faCheck}
								/>
							</button>
						</form>
					</div>
					<div className={styles["test-footer"]}>
						{"Tiến độ : " +
							(tempQTestArrayID.current.length / datas.length) *
								100 +
							" %"}
						{tempQTestArrayID.current.length / datas.length ===
							1 && (
							<button
								type='reset'
								onClick={() => {
									ModifyData(
										"tags",
										tag._id,
										"QTestArrayID",
										[]
									).then(() => {
										setIsReset(false);
										tempQTestArrayID.current = [];
										console.log(
											"reset temp : ",
											tempQTestArrayID.current
										);
										setQuestion(genQuestion(datas));
									});
								}}
							>
								reset
							</button>
						)}
					</div>
				</div>
			)}
		</div>
	);
}

function TagScreen(props, ref) {
	// luu gia tri nhan duoc khi click item cua tags ben sidebar
	// tag giup xac dinh target luu data (them tu moi vao dung nhom)
	const [tag, setTag] = useState("");
	const listRef = useRef();
	const F = useRef();
	const [toggleMode, setToggleMode] = useState("viewer");
	let [datas, setDatas] = useState([]);
	let [count, setCount] = useState(0);
	useImperativeHandle(ref, () => ({
		setTag,
	}));
	// tag thay doi => load lai data
	useEffect(() => {
		getDataFormIndexDb(tag._id).then(setDatas);
	}, [tag, toggleMode]);
	let viewDetailRef = useRef();
	return (
		<>
			<div className={styles["wrapper"]}>
				{toggleMode === "viewer" && (
					<>
						<ViewDetail ref={viewDetailRef} fn={ModifyData} />
						<div ref={listRef} className={styles["list-wrapper"]}>
							<AutoSizer>
								{({ height, width }) => {
									let numcol = 4;
									if (width < 920) {
										numcol = 3;
									}
									if (width < 645) {
										numcol = 2;
									}
									if (width < 375) {
										numcol = 1;
									}
									// convert 1d array to numcol d
									let reshapeData = splitArray(datas, numcol);
									return (
										<Grid
											className='List'
											columnWidth={
												width / numcol -
												GUTTER_SIZE / numcol
											}
											rowHeight={height / 2}
											innerElementType={forwardRef(
												({ style, ...rest }, ref) => (
													<div
														ref={ref}
														style={{
															...style,
															paddingLeft:
																GUTTER_SIZE,
															paddingTop:
																GUTTER_SIZE,
														}}
														{...rest}
													/>
												)
											)}
											rowCount={reshapeData.length}
											columnCount={numcol}
											height={height}
											width={width}
										>
											{({
												columnIndex,
												rowIndex,
												style,
											}) => {
												let data =
													reshapeData[rowIndex][
														columnIndex
													];
												return (
													data && (
														<div
															key={data._id}
															style={{
																...style,
																left:
																	style.left +
																	GUTTER_SIZE,
																top:
																	style.top +
																	GUTTER_SIZE,
																width:
																	style.width -
																	GUTTER_SIZE,
																height:
																	style.height -
																	GUTTER_SIZE,
															}}
															className={
																styles[
																	"list-item"
																]
															}
														>
															<div
																className={
																	styles[
																		"item-content"
																	]
																}
															>
																<div
																	className={
																		styles[
																			"hanzi"
																		]
																	}
																	onClick={() => {
																		const utterThis =
																			new SpeechSynthesisUtterance(
																				data.hanzi
																			);
																		utterThis.lang =
																			"zh-CN";
																		speechSynthesis.speak(
																			utterThis
																		);
																	}}
																>
																	{data.hanzi}
																</div>
																<div
																	className={
																		styles[
																			"pinyin"
																		]
																	}
																>
																	{
																		data.pinyin
																	}
																</div>
																<p
																	className={
																		styles[
																			"description"
																		]
																	}
																>
																	{
																		data.Vietnamese_meaning
																	}
																</p>
															</div>

															<div
																className={
																	styles[
																		"card-btns"
																	]
																}
															>
																<input
																	type='checkbox'
																	defaultChecked={
																		data.is_a_learned_word
																	}
																	onChange={(
																		e
																	) => {
																		ModifyData(
																			"datas",
																			data._id,
																			"is_a_learned_word",
																			e
																				.target
																				.checked
																		);
																	}}
																/>
																<div
																	className={
																		styles[
																			"btns"
																		]
																	}
																>
																	<button
																		onClick={() => {
																			// Th : xóa từ đã học => xóa id trong QTestArrayID
																			if (
																				data.is_a_learned_word
																			) {
																				ModifyData(
																					"tags",
																					tag._id,
																					"QTestArrayID",
																					tag.QTestArrayID.filter(
																						(
																							id
																						) =>
																							id !==
																							data._id
																					)
																				);
																			}
																			DeleteItem(
																				data._id
																			).then(
																				() => {
																					toast.info(
																						"deleted a item",
																						{
																							POSITION:
																								toast
																									.POSITION
																									.BOTTOM_CENTER,
																						}
																					);
																					getDataFormIndexDb(
																						tag._id
																					).then(
																						setDatas
																					);
																				}
																			);
																		}}
																	>
																		<FontAwesomeIcon
																			icon={
																				faTrashCan
																			}
																		/>
																	</button>
																	<button
																		onClick={() => {
																			viewDetailRef.current?.setViewDetail(
																				data
																			);
																		}}
																	>
																		<span>
																			<FontAwesomeIcon
																				icon={
																					faInfoCircle
																				}
																			/>
																		</span>
																	</button>
																</div>
															</div>
														</div>
													)
												);
											}}
										</Grid>
									);
								}}
							</AutoSizer>
						</div>
					</>
				)}
				{toggleMode === "addData" && <Form ref={F} tag={tag} />}

				{/*- truyền mảng các từ liên kết với tag vào test 
					-QTestArrayID dùng để lọc các từ đã test
					-trong QTest cpn thêm nút reset click => set lại QTestArrayID=[] (thao tác với tags table)
					- tính phần trăm số từ đã test  = QTestArrayID.length / datas.length *100
					- nếu từ đã test bị xóa => xóa id trong mảng QTestArrayID (dùng hàm modify và check bằng includes method)
					- nếu từ chưa test bị xóa => xóa bt
				*/}
				{toggleMode === "test" && <QTest tag={tag} datas={datas} />}
				<div className={styles["btns"]}>
					<button
						onClick={() => {
							setToggleMode((prev) => {
								if (prev === "viewer") return "addData";
								if (prev === "addData") return "viewer";
							});
						}}
					>
						<FontAwesomeIcon icon={faEye} />
					</button>
					<button
						onClick={() => {
							setToggleMode((prev) => {
								if (prev === "viewer") return "test";
								if (prev === "test") return "viewer";
							});
						}}
					>
						<FontAwesomeIcon icon={faArrowRight} />
					</button>
					<div className={styles["search-wrapper"]}>
						<form
							onSubmit={(e) => {
								e.preventDefault();
								const formData = new FormData(e.target);
								const data = Array.from(
									formData.entries()
								).reduce(
									(memo, [key, value]) => ({
										...memo,
										[key]: value,
									}),
									{}
								);
								filterData(
									data["search-type"],
									data["search-value"]
								).then(({ data, count }) => {
									setDatas(data);
									setCount(count);
								});
							}}
						>
							<div className={styles["search-types"]}>
								<select name='search-type'>
									<option value='hanzi' defaultValue='hanzi'>
										hanzi
									</option>
									<option value='pinyin'>pinyin</option>
									<option value='Vietnamese_meaning'>
										Vietnamese
									</option>
								</select>
							</div>
							<div className={styles["input-wrapper"]}>
								<input
									type='search'
									name='search-value'
									placeholder='search ...'
								/>
								<button type='submit'>
									<FontAwesomeIcon icon={faSearch} />
								</button>
							</div>
							<div>{count} items found</div>
						</form>
					</div>
					<label className={styles["slide-input"]}>
						<span>-</span>
						<input
							onChange={(e) => {
								listRef?.current.style.setProperty(
									"--Hanzi_FontSize",
									`${e.target.value}rem`
								);
							}}
							type='range'
							defaultValue={2.5}
							step={0.5}
							min={1}
							max={5}
						/>
						<span>+</span>
					</label>
				</div>
			</div>
		</>
	);
}

export default forwardRef(TagScreen);
